<template>
	<view>
		<u-select
			v-model="drugsPickerShow" 
			:list="allDrugsList" 
			@confirm="handlePickerConfirm"
		></u-select>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				drugsList: [], //选择后的列表
				drugsPickerShow: false, //药品选择器显示
				allDrugsList: [ //药品选择器中列表数据
					{
						label: '千斤藤',
						value: '千斤藤'
					},
					{
						label: '金线风',
						value: '金线风'
					},
					{
						label: '换骨筋',
						value: '换骨筋'
					},
				],
			}
		},
		onLoad() {

		},
		methods: {
			handlePickerConfirm(data) {
				this.drugsList.push(data[0])
			},
		},
	}
</script>

<style scoped>
	.list-cell {
		display: flex;
		box-sizing: border-box;
		width: 100%;
		padding: 10px 24rpx;
		overflow: hidden;
		color: #323233;
		font-size: 14px;
		line-height: 24px;
		background-color: #fff;
	}
</style>
